body {
  font-family: Arial, sans-serif;
  background-color: #f4f7fa;
  color: #333;
}

.navbar {
  background-color: #007bff;
  padding: 15px;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-title {
  color: white;
  font-size: 24px;
}

.navbar-links a {
  color: white;
  margin: 0 15px;
  text-decoration: none;
}

.navbar-links a:hover {
  text-decoration: underline;
}

.container {
  padding: 20px;
}

.ticket-form, .ticket-list {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #218838;
}

.ticket-item {
  padding: 15px;
  border-bottom: 1px solid #ddd;
}

.ticket-item h3 {
  margin: 0 0 10px;
}

.ticket-item button {
  margin-right: 10px;
}
